import { Header } from "@/components/header"
import { Footer } from "@/components/footer"
import Link from "next/link"
import { getProductBySlug } from "@/lib/products"
import { ArrowRight, Check } from "lucide-react"

type Props = { params: { slug: string } }

export default function ProductPage({ params }: Props) {
  const rawSlug = params?.slug
  const slug = Array.isArray(rawSlug) ? rawSlug[0] : rawSlug ?? ""
  const product = slug ? getProductBySlug(slug) : undefined

  if (!product) {
    return (
      <div className="min-h-screen">
        <Header />
        <main className="py-32 text-center">
          <h1 className="text-3xl font-bold">Product coming soon</h1>
          <p className="mt-4 text-muted-foreground">We're still working on this product. Check back later.</p>
          <div className="mt-6">
            <Link href="/products" className="text-accent underline">Back to products</Link>
          </div>
        </main>
        <Footer />
      </div>
    )
  }

  return (
    <div className="min-h-screen">
      <Header />

      <main className="py-24">
        <div className="mx-auto max-w-4xl px-6 lg:px-8">
          <div className="text-center mb-8">
            <h1 className="text-4xl font-bold mb-2">{product.name}</h1>
            <p className="text-lg text-accent font-medium">{product.tagline}</p>
            <p className="mt-4 text-muted-foreground">{product.description}</p>
          </div>

          {product.features && product.features.length > 0 && (
            <section className="mt-8">
              <h2 className="text-2xl font-bold mb-4">Key Features</h2>
              <ul className="grid grid-cols-1 sm:grid-cols-2 gap-3">
                {product.features.map((f, i) => (
                  <li key={i} className="flex items-start gap-3">
                    <Check className="h-5 w-5 text-accent mt-1" />
                    <span className="text-sm text-muted-foreground">{f}</span>
                  </li>
                ))}
              </ul>
            </section>
          )}

          <section className="mt-10 flex items-center justify-center">
            {product.status === "available" ? (
              <Link href="/contact" className="inline-flex items-center gap-2 bg-accent text-accent-foreground px-6 py-3 rounded-md">
                Get {product.name}
                <ArrowRight className="h-4 w-4" />
              </Link>
            ) : (
              <span className="inline-flex items-center gap-2 px-6 py-3 rounded-md border text-muted-foreground">In Development</span>
            )}
          </section>
        </div>
      </main>

      <Footer />
    </div>
  )
}
